import "core-js/modules/es.array.unshift.js";
import { getSearchMusic } from '@/request/api/home';
export default {
  name: 'Search',
  data() {
    return {
      keyWorldList: [],
      searchKey: '',
      searchList: []
    };
  },
  methods: {
    async enterKey() {
      if (this.searchKey) {
        this.keyWorldList.unshift(this.searchKey);
        //去重
        this.keyWorldList = [...new Set(this.keyWorldList)];
        if (this.keyWorldList.length > 10) {
          this.keyWorldList.splice(this.keyWorldList.length - 1, 1);
        }
        localStorage.setItem('keyWorldList', JSON.stringify(this.keyWorldList));
        let res = await getSearchMusic(this.searchKey);
        console.log(res.data.result.songs);
        this.searchList = res.data.result.songs;
        // if (!this.searchList) {
        //   console.log('对不起，没有搜索结果')
        // }
        this.searchKey = '';
      }
    },
    delHistory() {
      if (confirm('是')) {
        localStorage.removeItem('keyWorldList');
        this.keyWorldList = [];
      }
    },
    async searchHistory(k) {
      let res = await getSearchMusic(k);
      console.log(res.data.result.songs);
      this.searchList = res.data.result.songs;
    },
    updateIndex(s) {
      s.al = s.album;
      s.al.picUrl = s.album.artist.img1v1Url;
      this.$store.commit('pushPlayList', s);
      this.$store.commit('updatedPlayListIndex', this.$store.state.PlayList.length - 1);
    }
  },
  mounted() {
    this.keyWorldList = JSON.parse(localStorage.getItem('keyWorldList')) || [];
  }
};